"use client";

import { useState, useEffect } from "react";
import {
  getGlobalSettings,
  updateGlobalSettings,
} from "@/actions/adminActions";
import { generateInvite } from "@/actions/generateInvite";
import { useSession } from "@/app/lib/auth-client";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Switch } from "@/components/ui/switch";

export default function AdminPanel() {
  const { data: session } = useSession();
  const [settings, setSettings] = useState({});
  const [loading, setLoading] = useState(true);
  const [updating, setUpdating] = useState(false);
  const [inviteLink, setInviteLink] = useState("");

  useEffect(() => {
    async function fetchSettings() {
      const data = await getGlobalSettings();
      setSettings(data);
      setLoading(false);
    }
    fetchSettings();
  }, []);

  const handleSave = async () => {
    setUpdating(true);
    await updateGlobalSettings(session, settings);
    setUpdating(false);
  };

  const handleGenerateInvite = async () => {
    const link = await generateInvite(session);
    setInviteLink(link);
  };

  if (!session || session.user.role !== "admin") {
    return <p>Unauthorized</p>;
  }

  if (loading) return <p>Loading settings...</p>;

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold">Admin Panel</h1>
      <div className="mt-4 grid grid-cols-2 gap-4">
        <div>
          <label className="font-semibold">Map Center Latitude</label>
          <Input
            type="number"
            value={settings.mapCenterLat}
            onChange={(e) =>
              setSettings({
                ...settings,
                mapCenterLat: parseFloat(e.target.value),
              })
            }
          />
        </div>
        <div>
          <label className="font-semibold">Map Center Longitude</label>
          <Input
            type="number"
            value={settings.mapCenterLng}
            onChange={(e) =>
              setSettings({
                ...settings,
                mapCenterLng: parseFloat(e.target.value),
              })
            }
          />
        </div>
        <div>
          <label className="font-semibold">Viewing Map Open to Visitors</label>
          <Switch
            checked={settings.mapOpenToVisitors}
            onCheckedChange={(value) =>
              setSettings({ ...settings, openToVisitors: value })
            }
          />
        </div>
        <div>
          <div>
            <label className="font-semibold">
              Voting on Reports Open to Visitors
            </label>
            <Switch
              checked={settings.votesOpenToVisitors}
              onCheckedChange={(value) =>
                setSettings({ ...settings, openToVisitors: value })
              }
            />
          </div>
          <div>
            <label className="font-semibold">
              Report Submission Open to Visitors
            </label>
            <Switch
              checked={settings.submitReportsOpen}
              onCheckedChange={(value) =>
                setSettings({ ...settings, submitReportsOpen: value })
              }
            />
          </div>
          <div>
            <label className="font-semibold">Registration Mode</label>
            <select
              value={settings.registrationMode}
              onChange={(e) =>
                setSettings({ ...settings, registrationMode: e.target.value })
              }
              className="border rounded-md p-2 w-full"
            >
              <option value="open">Open</option>
              <option value="invite-only">Invite Only</option>
            </select>
          </div>
        </div>
        <Button onClick={handleSave} className="mt-4" disabled={updating}>
          {updating ? "Saving..." : "Save Changes"}
        </Button>
      </div>
      <div className="flex flex-col items-center mt-8">
        <Button onClick={handleGenerateInvite} className="mt-4">
          Generate Invite Link
        </Button>

        {inviteLink && (
          <p className="text-green-600 mt-2">
            Share this link:{" "}
            <a href={inviteLink} className="underline">
              {inviteLink}
            </a>
          </p>
        )}
      </div>
    </div>
  );
}
